.page {
  margin: 0;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  width: 200px;
  padding: 32px 16px 16px;
  font-family: Roboto, Helvetica Neue, Helvetica, sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
  display: flex;
}

.card__product {
  margin-bottom: 16px;
}

.product {
  color: #060b35;
  flex-direction: column;
  display: flex;
}

.product__image {
  margin-bottom: 40px;
  background-image: url("imac.202c52ff.jpeg");
  background-size: 100% 100%;
  width: 160px;
  height: 134px;
  margin-inline: auto;
}

.product__title {
  margin-block: 0 4px;
  font-size: 12px;
  font-weight: 500;
}

.product__code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
}

.product__rating {
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 24px;
  display: flex;
}

.product__stars {
  display: flex;
}

.product__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.product__star:not(:last-child) {
  background-image: url("star-active.edda4c43.svg");
  margin-right: 4px;
}

.product__reviews {
  font-size: 10px;
  line-height: 14px;
}

.product__cost {
  justify-content: space-between;
  display: flex;
}

.product__price {
  color: #616070;
}

.product__value {
  font-size: 16px;
  font-weight: 700;
}

.buy__link {
  box-sizing: border-box;
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border-radius: 5px;
  padding: 12px 70px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: block;
}

.buy__link:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}
